import React, { lazy, useEffect, useState } from 'react'
import { AnimationWrapper } from 'react-hover-animation'
import {ReactComponent as Empat} from './lima.svg'
import {ReactComponent as Sepuluh} from './sepuluh.svg'
import {ReactComponent as Tujuh} from './tujuh.svg'
import {
  CBadge,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CWidgetProgressIcon,
  CLink,
  CCol,
  CProgress,
  CRow,
  CCallout,
  CDataTable
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {fieldviavalen} from './source.js'

import MainChartExample from '../charts/MainChartExample.js'

const WidgetsDropdown = lazy(() => import('../widgets/WidgetsDropdown.js'))
const WidgetsBrand = lazy(() => import('../widgets/WidgetsBrand.js'))

const Beranda = (props) => {
  return (
    <>
    <CRow>
    <AnimationWrapper className="col-3">
    <CWidgetProgressIcon
            header="Form"
            text="Input Data Inspeksi Kapal"
            style={{backgroundColor:'#17335e'}}
            inverse
            value="0"
            >
              <Empat/>
              <CLink to='/helpkapal'class="stretched-link"></CLink>
        </CWidgetProgressIcon>
    </AnimationWrapper> 
    <AnimationWrapper className="col-3">
    <CWidgetProgressIcon
            header="Form"
            text="Tindak Lanjut Rectification Plan"
            style={{backgroundColor:'#17335e'}}
            inverse
            value="0"
            >
              <Sepuluh/>
              <CLink to='/tindaklanjut'class="stretched-link"></CLink>
    </CWidgetProgressIcon>
    </AnimationWrapper>
    <AnimationWrapper className="col-3"> 
    <CWidgetProgressIcon
            header="Dashboard"
            text="Monitoring Detensi Kapal Indonesia"
            style={{backgroundColor:'#17335e'}}
            inverse
            value="0"
            >
              <Tujuh/>
              <CLink to='/monitoring'class="stretched-link"></CLink>              
        </CWidgetProgressIcon>
    </AnimationWrapper>     
    </CRow>       
    </>
  )
}

export default Beranda
